@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@500&display=swap');
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Roboto Mono";
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.topBar{
  height: 50px;    
  vertical-align: bottom;
}
.topBar img{
  height: 50px;
  float: left;
  margin-left: 5vw;  
}
.topBarText{
  font-size: 20px;
  float: left;
  box-sizing: border-box;
  padding-top: 20px;
}
.appContainer{
  width: 80vw;    
  margin-left: 10vw;
  margin-top: 20px;
  box-shadow: 0 0 10px rgb(113, 110, 121);
  background-color: rgb(186, 183, 192);
  border-radius: 5px;
}
.tab{
  display: inline-block;
  background-color: rgb(247, 247, 247);
  width: 25%;
  height: 30px;
  border-radius: 5px 5px 0 0;
  cursor: pointer;
  line-height: 30px;
  border-bottom: 2px solid rgb(146, 147, 158);
  box-sizing: border-box;
  margin-bottom: -4px;
  font-size: 12px;
}
.tab:hover{
  background-color: rgb(213, 220, 255);  
  box-sizing: border-box;
}
.tabActive{
  background-color: rgb(225, 226, 231);
  border-bottom: 0px solid gray;
  border-top: 2px solid rgb(146, 147, 158);
  border-right: 2px solid rgb(146, 147, 158);
  border-left: 2px solid rgb(146, 147, 158);
  box-sizing: border-box;
}
.tabRight{
  border-radius: 5px 5px 0 3px;
}
.tabActive:hover{
  background-color: rgb(225, 226, 231);  
}
.mainWIndow{
  border: 2px solid rgb(225, 226, 231);  
  height: 70vh;
  box-sizing: border-box;
  padding: 20px;
  background-color: rgb(225, 226, 231);
  border-bottom: 2px solid rgb(146, 147, 158);
  border-right: 2px solid rgb(146, 147, 158);
  border-left: 2px solid rgb(146, 147, 158);
  border-radius: 0 0 5px 5px;
}
@media screen and (max-width: 400px){
  .tab{
    font-size: 9px;    
  }
  .appContainer{
    
  }
}